@font-face{
  font-family: 'Roboto';
  src: url('./../../assets/fonts/Roboto-Light.ttf');
  font-weight: normal;
  font-style: normal;
  }

.login__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(196, 196, 196, 0.6);
  z-index: 100;
  padding-top: 2vh;
}
.login {
  position: relative;
  margin: 25vh auto;
  height: 50vh;
  width: 60vw;
  background: rgba(255, 255, 255, 0.9);
  font-family: Roboto;
  font-size: 4vh;
  line-height: 125%;

}
.login__h2 {
  position: relative;
  top: 2vh;
  height: 8vh;
  margin: 4vh 0;
  text-align: center;
}
.login__input {
  margin-left: 6vh;
  margin-bottom: 2vh;
  height: 8vh;
  width: 45vw;
  border: none;
  border-bottom: 1px solid black;
  font-size: inherit;
  background-color: transparent;
}
.login__footer {
  position: absolute;
  display: flex;
  justify-content: space-around;
  align-items: center;
  left: 0;
  bottom: 0;
  height: 10vh;
  width: 100%;
  background-color: #4fbe79;
  padding: 0 2vw;
}
.login__button {
  width: 18vw;
  height: 6vh;
  border: 1px solid #ffffff;
  border-radius: 5px;
  background-color: transparent;
  font-size: 2vh;
  color: #ffffff;
  cursor: pointer;
}
.login__button_cancel {
  background-color: #E53935;
}
.hidden {
  display: none;
}
@media  screen and (max-width: 500px) {
  .login {
    font-size: 2vh;
  }
}
