@font-face {
  font-family: "Roboto";
  src: url("./../../assets/fonts/Roboto-Light.ttf");
  font-weight: normal;
  font-style: normal;
}
.admin {
  font-family: Roboto;
  min-width: 320px;
}
.admin__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 9vh;
  max-height: 100px;
  background-color: #00bf82;
}
.header__nav {
  display: flex;
  align-items: center;
  justify-content: start;
  width: fit-content;
  min-width: 200px;
  height: 100%;
}
.header__link {
  width: 24vw;
  max-width: 240px;
  text-align: center;
  text-decoration: none;
}
.header__button {
  height: 100%;
  width: 21vw;
  max-width: 210px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.header__button,
.header__link,
.card-category__title,
.card-category__text,
.card-category__button {
  color: #ffffff;
  font-size: 22px;
  line-height: 125%;
  font-weight: 700;
}
.header__button:hover,
.header__link:hover {
  color: red;
}
.underline {
  text-decoration: underline;
  pointer-events: none;
}
.events-none {
  pointer-events: none;
}

.admin__main {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: space-between;
  padding: 3%;
}
.card-category {
  position: relative;
  height: 240px;
  width: 361px;
  min-width: 300px;
  border: 1px solid #c4c4c4;
  border-radius: 5px;
  margin-top: 40px;
  padding-bottom: 25px;
  overflow: hidden;
}
.card-category__delete {
  position: absolute;
  top: 0;
  right: 0;
  width: 43px;
  height: 43px;
  background-image: url(./../../assets/plus.png);
  background-position: center;
  background-size: cover;
  cursor: pointer;
}
.card-category__title,
.card-category__text {
  color: black;
  height: 30px;
}
.card-category__title {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 0;
}
.card-category__text,
.card-category__input {
  font-weight: 400;
  margin-left: 10%;
  margin-top: 30px;
}
.card-category__buttons-container {
  position: absolute;
  bottom: 20px;
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  height: 85px;
  width: 100%;
}
.card-category__button {
  width: 40%;
  height: 34px;
  border-radius: 5px;
  border: 1px solid #00bf82;
  color: #00bf82;
  background-color: transparent;
  cursor: pointer;
  font-weight: 400;
}
.card-category__link {
  width: 40%;
  height: 34px;
}
.card-category__button-add-word {
  width: 100%;
}
.card-category__button-add-new {
  width: 100%;
  height: 80%;
  background-image: url(./../../assets/big-plus.png);
  background-position: center;
  background-size: 25%;
  background-repeat: no-repeat;
  cursor: pointer;
}
@media screen and (max-width: 450px) {
  .header__button,
  .header__link,
  .card-category__title,
  .card-category__text,
  .card-category__button {
    font-size: 18px;
    font-weight: 500;
  }
}
.card-category__input {
  width: 75%;
  border: none;
  border-bottom: 1px solid #3f51b5;
  font-size: inherit;
  background-color: transparent;
  margin-top: 90px;
}
.card-category__small-text {
  position: absolute;
  top: 70px;
  left: 10%;
  color: #3f51b5;
  font-size: 9px;
}
.card-category__button_update {
  height: 60px;
}
.card-category__button_cancel {
  border: 1px solid red;
  color: red;
}

.text-h4 {
  width: 300px;
  color: red;
}
