.card-word {
  position: relative;
  height: 455px;
  max-height: 70vh;
  width: 361px;
  min-width: 300px;
  border: 2px solid #C4C4C4;
  border-radius: 5px;
  margin-top: 40px;
  padding-bottom: 25px;
  overflow: hidden;
  font-size: 22px;
}
.card-word__wrapper {
  width: 253px;
  margin: 20px auto 10px;
  overflow: hidden;
}
.card-word__delete-button {
  position: absolute;
  top: 0;
  right: 0;
  width: 43px;
  height: 43px;
  background-image: url(./../../assets/plus.png);
  background-position: center;
  background-size: cover;
  cursor: pointer;
}
.card-word__property, 
.card-word__text {
  font-family: Roboto;
}
.card-word__property,
.card-word__new-word {
  margin-bottom: 15px;
  line-height: 125%;
  font-weight: 700;
}
.card-word__text {
  font-weight: 400;
}
.card-word__image {
  width: 100%;
  margin-top: 10px;

}
.card-word__button {
  width: 100%;
  height: 60px;
  border-radius: 5px;
  border: 1px solid #00BF82;
  color: #00BF82;
  background-color: transparent;
  cursor: pointer;
  font-weight: 400;
  text-align: center;
  padding-top: 15px;
}
.card-word__button-add-new {
  width: 100%;
  height: 80%;
  background-image: url(./../../assets/big-plus.png);
  background-position: center;
  background-size: 40%;
  background-repeat: no-repeat;
  cursor: pointer;
}
@media screen and (max-width: 450px) {
  .card-word__title, 
  .card-word__text,
  .card-word__button {
    font-size: 18px;
    font-weight: 500;
  }
}

.card-word__new-word {
  font-size: 36px;
  text-align: center;
  margin-top: 40px;
}
.card-word__text_sound:hover {
  color: blue;
  cursor: pointer;
}
